# Base
--------------------------------------------------------------*/
/* Typography
--------------------------------------------- */
body,
button,
input,
select,
optgroup,
textarea {
	color: #404040;
	font-family: "Manrope", sans-serif;
	font-size: 1rem;
	line-height: 1.5;
}

.hero-banner h1 {
	font-family: "Coustard", serif;
}

.section-text-heading-home-1 {
	font-size: 2rem;
	color: #6f4e37;
	background-image: url("https://crustandcrumb.bcitwebdeveloper.ca/wp-content/uploads/2023/11/squiggle-1.svg");
	background-repeat: no-repeat;
	background-position: -30%;
	background-size: 200px 150px;
	width: 250px;
}

.section-text-heading-home-2 {
	font-size: 2rem;
	color: #6f4e37;
	background-image: url("https://crustandcrumb.bcitwebdeveloper.ca/wp-content/uploads/2023/11/squiggle-1.svg");
	background-repeat: no-repeat;
	background-position: center;
	background-size: 170px 150px;
	width: fit-content;
	font-weight: 200;
	margin-left: 2.8rem;
}

.our-locations .section-text-heading-home-3 {
	font-size: 2rem;
	color: #fed8b1;
	background-image: url("https://crustandcrumb.bcitwebdeveloper.ca/wp-content/uploads/2023/11/squiggle-2.svg");
	background-repeat: no-repeat;
	background-position: center;
	background-size: 150px 80px;
	width: fit-content;
	font-weight: 200;
	margin-left: 2.8rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
}

h1 {
	font-family: "Coustard", "serif";
	font-weight: 300;
}

p {
	margin-bottom: 1.5em;
}

dfn,
cite,
em,
i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", courier, monospace;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code,
kbd,
tt,
var {
	font-family: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
}

abbr,
acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark,
ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

/* Elements
--------------------------------------------- */
body {
	background-color: white;
}

body.page-id-13,
body.post-type-archive-product {
	background: linear-gradient(0deg, rgb(111, 78, 55) 0%, rgb(255, 255, 255) 100%);
}

main {
	width: 100%;
	max-width: 1920px !important;
	margin-left: auto !important;
	margin-right: auto !important;
}

html {
	scroll-behavior: smooth;
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul,
ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: 700;
}

dd {
	margin: 0 1.5em 1.5em;
}

.our-locations h2 {
	font: "Manrope", sans-serif;
	color: white;
	font-size: calc(1em + 1.2vw);
	margin-top: 3rem;
	margin-left: 0;
}
@media (max-width: 48em) {
	.our-locations h2 {
		font-size: calc(1.6rem + 1.75vw);
	}
}
.our-locations .store-locations {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	gap: 3rem;
	justify-content: center;
	padding: 1em;
}
.our-locations .store-locations .store {
	width: 17rem;
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	flex-shrink: 0;
}
@media (max-width: 64em) {
	.our-locations .store-locations .store {
		flex-direction: row;
		width: 20rem;
	}
}
.our-locations .store-locations .store p {
	font-family: "Manrope", sans-serif;
	color: white;
	font-size: calc(1em + 0.3vw);
	margin-top: 0;
	text-align: center;
}
.our-locations .store-locations .store img {
	box-shadow: 0px 0px 5px 0px black;
}
.our-locations .location-more-info {
	text-align: center;
}
.our-locations .location-more-info a {
	padding: 0.7rem 3rem;
	background-color: #fed8b1;
	color: black;
}
.our-locations .location-more-info a:hover {
	color: #fff;
}
@media (max-width: 48em) {
	.our-locations .location-more-info {
		margin-left: 0;
	}
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

img {
	height: auto;
	max-width: 100%;
}

figure {
	margin: 1em 0;
}

table {
	margin: 0 0 1.5em;
	width: 100%;
}

/*general styling*/
.accordionTitle {
	cursor: pointer;
	background-color: #6f4e37;
	padding-left: 1rem;
}

.accordionContent {
	display: none;
}

.accordionTitle.is-open ~ .accordionContent {
	display: block;
}

.accordionTitle {
	margin: 0;
	display: flex;
	justify-content: space-between;
	align-content: center;
}

.accordion-icon {
	margin-right: 16px;
}
.accordion-icon svg {
	width: 14.16px;
	height: 18px;
}
.accordion-icon svg path {
	fill: white;
}

/*contact*/
.page-id-27 .wrapper {
	margin: 0 16px;
	border: 1px solid white;
	border-radius: 4px;
	margin-bottom: 9.96px;
}
.page-id-27 .accordionContent {
	display: flex;
	flex-direction: column;
	max-height: 0;
	overflow: hidden;
	max-width: 515px;
	transition: max-height 0.5s linear;
	margin: 16px auto 0;
	padding: 0 16px;
}
.page-id-27 .accordionTitle.is-open ~ .accordionContent {
	display: flex;
	flex-direction: column;
	transition: max-height 0.5s linear;
	max-height: 400px;
	max-width: 515px;
	margin: 16px auto 0;
	padding: 0 16px;
}

@media (min-width: 48em) {
	.page-id-27 .accordion-icon {
		display: none;
	}
	.page-id-27 .wrapper {
		width: 100%;
		border-radius: 0;
		border: none;
		margin-bottom: 0;
	}
	.page-id-27 .accordionTitle {
		cursor: auto;
		padding-left: 0;
	}
	.page-id-27 .accordionContent {
		overflow: visible;
		display: grid;
		max-height: fit-content;
	}
	.page-id-27 .accordionTitle.is-open ~ .accordionContent {
		margin: 0;
		padding: 0;
		display: grid;
	}
	.page-id-27 .accordionTitle.is-open ~ .location-address {
		margin: 16px 0 24px;
	}
}
/*careers*/
.post-type-archive-cac-careers #filtered-jobs {
	display: flex;
	flex-direction: column;
}
.post-type-archive-cac-careers .fade-up {
	opacity: 0;
	transform: translateY(20px);
	transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}
.post-type-archive-cac-careers .fade-up.fade-up-visible {
	opacity: 1;
	transform: translateY(0);
}
.post-type-archive-cac-careers .accordion-container {
	border: 1px solid white;
	border-radius: 4px;
}
.post-type-archive-cac-careers .accordionTitle {
	margin: 0px;
	cursor: pointer;
	border-radius: 4px;
}
.post-type-archive-cac-careers .accordionContent {
	color: black;
	margin-top: -5px;
	border: 2px solid #6f4e37;
	border-radius: 4px;
	width: 100%;
	padding: 0 16px;
	display: block;
	max-height: 0;
	overflow: hidden;
	transition: max-height 0.8s ease-in-out;
}
.post-type-archive-cac-careers .accordionTitle.is-open ~ .accordionContent {
	text-align: start;
	display: block;
	transition: max-height 0.8s ease-in-out;
	max-height: 1000px;
}

/* Links
--------------------------------------------- */
a {
	color: #000000;
	cursor: pointer;
}
a:visited {
	color: #a67b5b;
}
a:hover, a:focus, a:active {
	color: #191970;
}
a:focus {
	outline: thin dotted;
}
a:hover, a:active {
	outline: 0;
}

/* Forms
--------------------------------------------- */
button,
input[type=button],
input[type=reset],
input[type=submit] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	border-radius: 3px;
	background: #e6e6e6;
	color: rgba(0, 0, 0, 0.8);
	line-height: 1;
	padding: 0.6em 1em 0.4em;
}
