/*--------------------------------------------------------------
# Layouts
--------------------------------------------------------------*/
.page-id-20,
.error404 {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
}
.page-id-20 .site-main,
.error404 .site-main {
	flex-grow: 1;
}

.post-type-archive-cac-careers #page .site-main {
	max-width: 64rem;
	margin: 0 auto;
	margin-top: 5.9rem;
	width: 100%;
}

/*--------------------------------------------------------------
# Components
--------------------------------------------------------------*/
/* Navigation
--------------------------------------------- */
.main-navigation {
	display: block;
	width: 100%;
}
.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-left: 0;
}
.main-navigation ul ul {
	box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
	float: left;
	position: absolute;
	top: 100%;
	left: -999em;
	z-index: 99999;
}
.main-navigation ul ul ul {
	left: -999em;
	top: 0;
}
.main-navigation ul ul li:hover > ul, .main-navigation ul ul li.focus > ul {
	display: block;
	left: auto;
}
.main-navigation ul ul a {
	width: 200px;
}
.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
	left: auto;
}
.main-navigation li {
	position: relative;
}
.main-navigation a {
	display: block;
	text-decoration: none;
}
/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;
}

@media screen and (min-width: 37.5em) {
	.menu-toggle {
		display: none;
	}
	.main-navigation ul {
		display: flex;
	}
}
.site-main .comment-navigation,
.site-main .posts-navigation,
.site-main .post-navigation {
	margin: 0 0 1.5em;
}
.comment-navigation .nav-links,
.posts-navigation .nav-links,
.post-navigation .nav-links {
	display: flex;
}
.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
	flex: 1 0 50%;
}
.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
	text-align: end;
	flex: 1 0 50%;
}

/* Posts and pages
--------------------------------------------- */
.page-id-27 .page-title {
	text-align: center;
	margin-top: 7.7rem;
}
.page-id-27 .location-name {
	color: white;
}
.page-id-27 .location-container {
	max-height: 39px;
	background-color: #fed8b1;
	border: 2px solid #6f4e37;
	border-radius: 4px;
	transition: max-height 0.5s linear;
}
.page-id-27 .location-container.container-open {
	transition: max-height 0.5s linear;
	max-height: 1200px;
}
.page-id-27 .location-img {
	width: 100%;
}

@media (min-width: 48em) {
	.page-id-27 .accordionTitle {
		background-color: transparent;
	}
	.page-id-27 .location-name {
		background-color: transparent;
		color: black;
	}
	.page-id-27 .accordionContent {
		margin: 0;
		padding: 0;
	}
	.page-id-27 .location-address {
		margin-top: 16px;
	}
	.store-locations {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.wrapper .location-container {
		margin: 3rem 0;
		background-color: transparent;
		display: grid;
		grid-template-rows: 50px 320px 50px;
		max-height: fit-content;
		border-radius: 0;
		border: 0;
		padding: 1rem 16px;
		width: 100%;
		justify-content: center;
		column-gap: 4%;
	}
	.wrapper:nth-child(odd) {
		grid-template-columns: 515px fit-content(515px);
	}
	.wrapper:nth-child(odd) div {
		grid-column: 2/2;
		grid-row: 2/3;
	}
	.wrapper:nth-child(odd) .location-img {
		grid-column: 1/2;
		grid-row: 1/1;
		box-shadow: 0px 0px 5px 0px black;
	}
	.wrapper:nth-child(odd) .location-address {
		grid-column: 1/1;
		grid-row: 3;
	}
	.wrapper:nth-child(3n) {
		background-color: #fed8b1;
	}
	.wrapper:nth-child(3n+1) {
		background-color: #6f4e37;
	}
	.wrapper:nth-child(3n+1) .accordionContent,
	.wrapper:nth-child(3n+1) .accordionTitle {
		color: white;
	}
	.wrapper:nth-child(3n+2) {
		background-color: transparent;
	}
	.wrapper:nth-child(even) {
		grid-template-columns: fit-content(515px) 515px;
	}
	.wrapper:nth-child(even) div {
		grid-column: 1/1;
		grid-row: 2/3;
	}
	.wrapper:nth-child(even) .location-img {
		grid-column: 2/2;
		grid-row: 1/1;
	}
	.wrapper:nth-child(even) .location-address {
		grid-column: 2/2;
		grid-row: 3;
	}
}
