button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  border: 1px solid;
  border-color: $color__border-button;
  border-radius: 3px;
  background: $color__background-button;
  color: rgba(0, 0, 0, 0.8);
  line-height: 1;
  padding: 0.6em 1em 0.4em;

  &:hover {
    border-color: $color__border-button-hover;
  }

  &:active,
  &:focus {
    border-color: $color__border-button-focus;
  }
}

//View Cart Button - General

.added_to_cart {
	@include button-customizable(
		$bg-color: white,
		$text-color: $text-primary-color,
		$padding: 0.5rem 1rem,
		$radius: 20px,
	);
	width: fit-content;
}

// all buttons on Homepage
.page-id-13 {
	// Hero Section and About Section
	.cta-button,
	.about-us-cta {
		@include button-customizable(
			$bg-color: $button-secondary-color,
		);
	}
	// CTA Location Section
	.check-location-cta {
		@include button-customizable(
			$bg-color: $button-primary-color,
			$text-color: $text-secondary-color,
		);
    
	}

  //Add-to-card in Featured Product
  .product-card .button {
    @include button-customizable(
      $bg-color: $button-primary-color,
      $text-color: $text-secondary-color
    );
    width: fit-content;
    align-self: flex-end;
  }
}

