// Center block
@mixin center-block {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

// Column width with margin
@mixin column-width($numberColumns: 3) {
  width: map-get($columns, $numberColumns) -
    (($columns__margin * ($numberColumns - 1)) / $numberColumns);
}

// Border-radius
@mixin border-radius-card {
  border-radius: 45deg;
}

// Buttons - CTAs
@mixin button-customizable(
  $bg-color: $button-primary-color,
  $radius: 10px,
  $text-color: black,
  $padding: 0.6rem
) {
  background-color: $bg-color;
  border-radius: $radius;
  color: $text-color;
  cursor: pointer;
  display: inline-block;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  padding: $padding;
  text-align: center;
  text-decoration: none;
  transition: all 0.2s ease-in-out;
  box-shadow: 0px 0px 3px 0px gray;

  @media (min-width: $small-screen) {
    &:hover {
      background-color: darken($bg-color, 10%);
    }

    &:active,
    &:focus {
      background-color: darken($bg-color, 20%);
    }
  }
}
// Order Form in Catering page
@mixin form-field-style {
  padding: 10px;
  margin-bottom: 5px;
  color: $color__text-input-focus;

  border: 1px solid $color__border-input;
  border-radius: $border__radius;
  background-color: $color__border-input;
  inline-size: 100% !important;
  box-sizing: border-box;
}
