/*general styling*/
.accordionTitle {
  cursor: pointer;
  background-color: $primary-color;
  padding-left: 1rem;
}
.accordionContent {
  display: none;
}
.accordionTitle.is-open ~ .accordionContent {
  display: block;
}
.accordionTitle {
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-content: center;
}
.accordion-icon {
  margin-right: $page-margin;
  //to match faq
  svg {
    width: 14.16px;
    height: 18px;
    path {
      fill: white;
    }
  }
}

/*contact*/
.page-id-27 {
  .wrapper {
    margin: 0 $page-margin;
    border: 1px solid white;
    border-radius: 4px;
    //to match the contact page margins
    margin-bottom: 9.96px;
  }
  .accordionContent {
    display: flex;
    flex-direction: column;
    max-height: 0;
    overflow: hidden;
    max-width: 515px;
    transition: max-height 0.5s linear;
    margin: 16px auto 0;
    padding: 0 $page-margin;
  }

  .accordionTitle.is-open ~ .accordionContent {
    display: flex;
    flex-direction: column;
    transition: max-height 0.5s linear;
    max-height: 400px;
    max-width: 515px;
    margin: 16px auto 0;
    padding: 0 $page-margin;
  }
}
@media (min-width: $small-screen) {
  .page-id-27 {
    .accordion-icon {
      display: none;
    }
    .wrapper {
      width: 100%;
      border-radius: 0;
      border: none;
      margin-bottom: 0;
    }
    .accordionTitle {
      cursor: auto;
      padding-left: 0;
    }
    .accordionContent {
      overflow: visible;
      display: grid;
      max-height: fit-content;
    }
    .accordionTitle.is-open ~ .accordionContent {
      margin: 0;
      padding: 0;
      display: grid;
    }
    .accordionTitle.is-open ~ .location-address {
      margin: 16px 0 24px;
    }
  }
}

/*careers*/
.post-type-archive-cac-careers {
  #filtered-jobs {
    display: flex;
    flex-direction: column;
  }
  // animation to mimic aos
  .fade-up {
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.6s ease-out, transform 0.6s ease-out;
  }
  .fade-up.fade-up-visible {
    opacity: 1;
    transform: translateY(0);
  }

  .accordion-container {
    border: 1px solid white;
    border-radius: 4px;
  }
  .accordionTitle {
    margin: 0px;
    cursor: pointer;
    border-radius: 4px;
  }
  .accordionContent {
    color: black;
    margin-top: -5px;
    border: 2px solid $primary-color;
    border-radius: 4px;
    width: 100%;
    padding: 0 $page-margin;
    display: block;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.8s ease-in-out;
  }
  .accordionTitle.is-open ~ .accordionContent {
    text-align: start;
    display: block;
    transition: max-height 0.8s ease-in-out;
    max-height: 1000px;
  }
}
