// Catering page

.page-id-32 {
  .catering-title {
    h1 {
      position: relative;
      display: flex;
      justify-content: center;
      color: $text-primary-color;
      margin-top: 7.7rem;
      margin-bottom: 24px;
    }
  }

  .opening-paragraph {
    padding: 2rem;
    background-color: $secondary-color;
    p {
      color: $text-primary-color;
      width: 75%;
      margin: 0 auto;
      padding: 0.5rem 0;
    }
    div:nth-child(2) {
      width: 75%;
      margin: 0 auto;
    }
    ol,
    ul {
      margin-left: 0;
      margin-bottom: 0;
      li {
        padding: 0.5rem 0;
      }
    }

    @media (max-width: $small-screen) {
      padding: 2rem 6vw 1rem 6vw;
    }
  }

  .catering-packages {
    display: flex;
    flex-direction: column;
    h2 {
      align-self: center;
      margin: 2rem 0;
    }
  }
}
